.schedule{
    height: 90vh;
    min-height: 800px;
    min-width: 1200px;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-gap: 0 2%;
    grid-template-columns:40px repeat(7, minmax(100px, 12vw));
}

.days{
    text-align: center;
}

.time{
    text-align: left;
    position: relative;
    width: 100%;
    height: 100%;
    border-top: 1px rgb(201, 195, 195) solid;
}
